import React from 'react';
import Header from './Header';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import img1 from "./img/arch1.jfif";
import img2 from "./img/birthday.gif";
import img3 from "./img/cook.gif";
import img4 from "./img/birthday4.gif";
import Image from 'react-bootstrap/Image';
import VideoPlayer from 'react-video-js-player'
import video1 from "./video/bakv1.mp4";
import vid2 from "./video/bakv2.mp4";
import img5 from "./img/birthday2.gif";
import img6 from "./img/birthday3.gif";
import trav1 from "./video/travel1.mp4";
import tra1 from "./img/tra1.gif";
import tra3 from "./img/tra3.gif";
import tra2 from "./img/tra2.gif";
import tra4 from "./img/tra4.gif";
import tra5 from "./img/tra5.gif";
import NavigationBar from './NavigationBar';

import { Redirect } from 'react-router';
import { useState, useEffect } from 'react'


function Business() {

    // if (sessionStorage.getItem('uid'))        return <Redirect to="/userafterlogin" />

    const [windowSize, setWindowSize] = useState(window.innerWidth);

    const handleSize = () => {
      const size = window.innerWidth;
      setWindowSize(size);
    };
  
    useEffect(() => {
      window.addEventListener("resize", handleSize);
    }, [])


    if (windowSize<1000) {
        return(<div>
            <NavigationBar/>

            To See that page open in desktop
            
            </div>)
    } else {
      return (
        <div>
             {/* <Header/> */}

             
             <NavigationBar/>
             <br></br>
             <div style={{backgroundColor:'royalblue'}}>
             <Container>
                 <br></br><br></br>
                <Row>
                    <Col style={{marginTop:'400px'}}><h1 style={{fontSize:'60px'}}>It pays to be<br></br>
                         positive
                        </h1><br></br>
                        <h3>Where people see your brand matters more than ever.</h3>
                        <h2 style={{color:'salmon'}}>Baking Ideas : </h2>
                        </Col>
                    <Col xs={8}>
                    <Row>
                        <Col><Image src={img5}  style={{width:'250px',borderRadius: '30px',height:'375px'}}/></Col>
                        <Col><Image src={img2}  style={{width:'250px',height:'375px',borderRadius: '30px'}}/></Col>
                        <Col><Image src={img6}  style={{width:'250px',height:'375px',borderRadius: '30px'}}/></Col>
                    </Row>
                    <br></br>
                    <Row>
                        <Col><Image src={img4}  style={{width:'250px',marginLeft:'120px',borderRadius: '30px',height:'375px'}}/></Col>
                        <Col><VideoPlayer src={video1} width="250px" height="370px"/></Col>
                     </Row>
                     <br></br>
                     <Row>
                        <Col><Image src={img3} style={{width:'250px',height:'375px',marginLeft:'280px',borderRadius: '30px'}}/></Col>
                    </Row>
                    </Col>
                </Row>
            </Container>
             </div>
             <div style={{backgroundColor:'royalblue'}}>
             <Container>
                 <br></br><br></br>
                <Row>
                    <Col style={{marginTop:'400px'}}><h1 style={{fontSize:'60px'}}>It pays to be<br></br>
                         positive
                        </h1><br></br>
                        <h3>Where people see your brand matters more than ever.</h3><br></br>
                        <h2 style={{color:'salmon'}}>Travel Ideas : </h2>
                        </Col>
                    <Col xs={8}>
                    <Row>
                        <Col><Image src={tra2}  style={{width:'250px',borderRadius: '30px',height:'375px'}}/></Col>
                        <Col><Image src={tra3}  style={{width:'250px',height:'375px',borderRadius: '30px'}}/></Col>
                        <Col><Image src={tra4}  style={{width:'250px',height:'375px',borderRadius: '30px'}}/></Col>
                    </Row>
                    <br></br>
                    <Row>
                        <Col><Image src={tra1}  style={{width:'250px',marginLeft:'120px',borderRadius: '30px',height:'375px'}}/></Col>
                        <Col><VideoPlayer src={trav1} width="250px" height="370px"/></Col>
                     </Row>
                     <br></br>
                     <Row>
                        <Col><Image src={tra5} style={{width:'250px',height:'375px',marginLeft:'280px',borderRadius: '30px'}}/></Col>
                    </Row>
                    </Col>
                </Row>
            </Container>
            
             </div>
             
        </div>
    )
}
}
export default Business
