import React, { useState } from 'react'
import axios from 'axios';
import NavigationBar from './NavigationBar';
import '../components/Reghooks.css';
import { Redirect } from 'react-router'

function Registration() {

    const [eimage, setEImage] = useState("");
    const [ename, setEmpName] = useState("");
    const [eemail, setEmpEmail] = useState("");
    const [emobile, setEmpmobile] = useState("");
    const [edob, setEmpDOB] = useState("");
    const [epass, setEmpPass] = useState("");
    const [egender, setEmpGender] = useState("");
    const [ecountry, setEmpCountry] = useState("");
    const [eaddress, setEmpAddress] = useState("");
    const [msg, setMessage] = useState("");

    const onChangeEImage = (e) => setEImage(e.target.value);
    const onChangeEmpName = (e) => setEmpName(e.target.value);
    const onChangeEmpEmail = (e) => setEmpEmail(e.target.value);
    const onChangeEmpMobile = (e) => setEmpmobile(e.target.value);
    const onChangeEmpDOB = (e) => setEmpDOB(e.target.value);
    const onChangeEmpPass = (e) => setEmpPass(e.target.value);
    const onChangeEmpGender = (e) => setEmpGender(e.target.value);
    const onChangeEmpCountry = (e) => setEmpCountry(e.target.value);
    const onChangeEmpAddress = (e) => setEmpAddress(e.target.value);

    const handleImage = async e => {
        e.preventDefault()
        let img = e.target.files[0]
        if (!img) return alert("File not exist.")
        //5242880 == 5 mb
        if (img.size > 1024 * 1024 * 10) return alert("Size too large!")
        if (img.type !== 'image/jpeg' && img.type !== 'image/png') return alert("File format is incorrect.")
    
        let data = new FormData()
        data.append('file', img)
        data.append('upload_preset', "mainak_image")
        data.append('cloud_name', "mainak487")
        fetch(' https://api.cloudinary.com/v1_1/mainak487/image/upload', {
          method: "post",
          body: data
        })
          .then(res => res.json())
          .then(data => {
            setEImage(data.url)
            console.log(data)
          })
          .catch(err => {
            console.log(err)
          })
      }


    const handleSubmit = (evt) => {
        evt.preventDefault();
        console.log(`Form submitted:`);
        console.log(`NAME: ${ename}`);
        console.log(`EMAIL: ${eemail}`);

        const empinfo = {
            empimage: eimage,
            empname: ename,
            empemail: eemail,
            empmobile: emobile,
            empdob: edob,
            emppass: epass,
            empgender: egender,
            empcountry: ecountry,
            empaddress: eaddress
        }

        axios.post('https://image-gallery-backend1.herokuapp.com/emp/register', empinfo)
            .then(res => {
                console.log(res.data)
                setMessage(res.data.message);
            });

        setEImage('')
        setEmpName('')
        setEmpEmail('')
        setEmpmobile('')
        setEmpDOB('')
        setEmpPass('')
        setEmpGender('')
        setEmpCountry('')
        setEmpAddress('')

    }


    return (
        <div>
            <NavigationBar />
            <br />
            <center>
                <h3>REGISTRATION FORM</h3>
            </center>
            <center><h4 style={{ color: "brown" }}> {msg}</h4></center>
            <center>
            <form onSubmit={handleSubmit} >


           

                 <strong >Name :</strong>
                <input type="text" value={ename}
                    onChange={onChangeEmpName} placeholder="Enter Name"
                    required style={{width:'50%'}} id="reg"/>
                <br /><br />
                <strong >Email :</strong>
                <input type="email" value={eemail}
                    onChange={onChangeEmpEmail} placeholder="Enter Email"
                    required style={{width:'50%'}} id="reg"/>
                <br /><br />


                <p>Profile Picture</p>
        <input type="file" className="form-control" placeholder="Image link"  onChange={handleImage} required style={{width:'50%'}}/><br/><br/>

                <strong >Mobile No :</strong>
                <input type="number" value={emobile}
                    onChange={onChangeEmpMobile} placeholder="Enter Mobile No"
                    required style={{width:'50%'}} id="reg"/>
                <br /><br />

                <strong >Date Of Birth :</strong>
                <input type="date" value={edob}
                    onChange={onChangeEmpDOB} style={{width:'50%'}} id="reg"/>
                <br /><br />

                <strong >Password :</strong>
                <input type="password" value={epass}
                    onChange={onChangeEmpPass} placeholder="Enter Password"
                    required style={{width:'50%'}} id="reg"/>
                <br /><br />

                <center><h5 style={{width:'50%'}}>Gender</h5></center>
                <center><p style={{width:'50%'}}>Male</p></center>
                <input type="radio" name="gender" value="MALE"
                    checked={egender === 'MALE'}
                    onChange={onChangeEmpGender} style={{width:'50%'}} id="reg"/>

                <center><p style={{width:'50%'}}>Female</p></center>
                <input type="radio" name="gender" value="FEMALE"
                    checked={egender === 'FEMALE'}
                    onChange={onChangeEmpGender} style={{width:'50%'}} style={{width:'50%'}} id="reg"/>
                <br /><br />

                <strong >Country :</strong><br/>
                <select value={ecountry} onChange={onChangeEmpCountry} style={{width:'50%'}} id="reg">
                    <option value="AF">Afghanistan</option>
                    <option value="India">India</option>
                    <option value="UK">UK</option>
                    <option value="USA">USA</option>
                </select>
                <br /><br />

                <strong >Address :</strong><br/>
                <textarea value={eaddress}
                    onChange={onChangeEmpAddress} rows="3" style={{width:'50%'}} id="reg">
                </textarea>
                <br /><br />

                <input type="submit" value="REGISTER" style={{width:'50%'}} style={{width:'50%'}} id="reg"/>

            </form>
            </center>
            
        </div>
    )
}


export default Registration
