import React, { useState } from 'react'
import axios from 'axios';
import NavigationBar from './NavigationBar';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col'

function Contactus() {
    const [ename, setEmpName] = useState("");
    const [eemail, setEmpEmail] = useState("");
    const [emobile, setEmpmobile] = useState("");
    const [emessage, setEmpMessege] = useState("");
    const [msg, setMessage] = useState("");

    const onChangeEmpName = (e) => setEmpName(e.target.value);
    const onChangeEmpEmail = (e) => setEmpEmail(e.target.value);
    const onChangeEmpMobile = (e) => setEmpmobile(e.target.value);
    const onChangeEmpMessage = (e) => setEmpMessege(e.target.value);
    // const onChangeEmpAddress = (e) => setEmpAddress(e.target.value);

    const handleSubmit = (evt) => {
        evt.preventDefault();
        console.log(`Form submitted:`);
        console.log(`NAME: ${ename}`);
        console.log(`EMAIL: ${eemail}`);

        const empinfo = {
            uname: ename,
            uemail: eemail,
            umobile: emobile,
            umessage: emessage
            // uaddress: eaddress
        }

        axios.post('https://image-gallery-backend1.herokuapp.com/usercontact/contact', empinfo)
            .then(res => {
                console.log(res.data)
                setMessage(<center>MESSEGE SEND SUCCESSFUL</center>)
            });

        setEmpName('')
        setEmpEmail('')
        setEmpmobile('')
        setEmpMessege('')
        // setEmpAddress('')

    }

    return (
        <div>
            <NavigationBar />
           

            <br />





            <Container>
                {/* Stack the columns on mobile by making one full-width and the other half-width */}
                <Row>
                    <Col xs={12} md={4}>
                        <center>
                            <h3>CONTACT US FORM</h3>
                        </center>
                        <h4 style={{ color: "brown" }}> {msg}</h4>
                        <center>
                            <form onSubmit={handleSubmit}>
                                <input type="text" value={ename}
                                    onChange={onChangeEmpName} placeholder="Enter Name"
                                    required style={{ width: '100%' }} />
                                <br /><br />

                                <input type="email" value={eemail}
                                    onChange={onChangeEmpEmail} placeholder="Enter Email"
                                    required style={{ width: '100%' }} />
                                <br /><br />

                                <input type="number" value={emobile}
                                    onChange={onChangeEmpMobile} placeholder="Enter Mobile No"
                                    required style={{ width: '100%' }} />
                                <br /><br />

                                <div class="form-group">
                                    <textarea class="form-control" rows="6" placeholder="Message" value={emessage} onChange={onChangeEmpMessage} required style={{ width: '100%', border: '1px solid black' }}></textarea>
                                </div>
                                <br /><br />

                                <input type="submit" value="Send Messege" style={{ width: '100%' }} /><br /><br />

                            </form>
                        </center>
                    </Col><br />
                    <Col xs={12} md={8}>
                        <iframe src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d1517.9410647794264!2d88.44110639338699!3d22.378253050747663!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3a026d0097533aed%3A0xe18407a46f2e97f6!2sGargi%20Memorial%20Institute%20of%20Technology!5e0!3m2!1sen!2sin!4v1635264941416!5m2!1sen!2sin" style={{ width: '100%', height: '600px', border: '0', allowfullscreen: '', loading: 'lazy' }}></iframe>
                        <br />
                    </Col>
                </Row>
            </Container>






        </div>
    )
}

export default Contactus;
