import React, { useState, useEffect } from 'react'
import axios from 'axios'
import NavigationBar from './NavigationBar';
import Table from 'react-bootstrap/Table';
import Container from 'react-bootstrap/Container';
import { Redirect } from 'react-router';

function DisplayAll() {
  const [emplist, setEmpList] = useState([]);

  //Similar to componentDidMount and componentDidUpdate
  useEffect(() => {
    axios.get('https://image-gallery-backend1.herokuapp.com/emp')
      .then(response => {
        console.log(response.data)
        setEmpList(response.data);
      })
      .catch((error) => {
        console.log(error);
      })
  }, [])

  function viewEmpList() {
    return emplist.map((currentrow, index) => {
      return (

        <tr key={index}>
          <td>{currentrow._id}</td>
          <td>{currentrow.empname}</td>
          <td>{currentrow.empemail}</td>
          <td><img src={currentrow.empimage} alt="" style={{borderRadius:'500px',width:'100px',height:'80px'}}/></td>
          <td>{currentrow.empmobile}</td>
          <td>{currentrow.empdob}</td>
          <td>{currentrow.empgender}</td>
          <td>{currentrow.empcountry}</td>
          <td>{currentrow.empaddress}</td>
        </tr>
        
      );
    });
  }

  let admin = sessionStorage.getItem('Key_Veriable')
  if (admin == null) {
    return (<Redirect to="/adminlogin" />)
  }
  return (
    <div>
      <NavigationBar />
      <br />
      <center>
      <h3>ALL USER DETAILS</h3>
      </center><br/>
      <Container style={{overflowX:'auto'}}>
      <Table striped bordered hover variant="primary">
        <thead>
          <tr>
            <th>ID</th>
            <th>Name</th>
            <th>Email</th>
            <th>User Image</th>
            <th>Mobile</th>
            <th>DOB</th>
            <th>Gender</th>
            <th>Country</th>
            <th>Address</th>
          </tr>
        </thead>

        <tbody>
          {viewEmpList()}
        </tbody>
        </Table>
        </Container>
    </div>
  )
}


export default DisplayAll