import React, { useState, useEffect } from "react";
// import { Redirect } from "react-router-dom";
import axios from 'axios';
import NavigationBar from './NavigationBar';
import Table from 'react-bootstrap/Table';
import Container from 'react-bootstrap/Container';
import { Redirect } from "react-router";

function UserViewImages() {
    const[imagelist,setImagesList] = useState([]);
    let uid = sessionStorage.getItem('uid');
    let authuser = sessionStorage.getItem('useremail'); 
    useEffect(() => {
        axios.get('https://image-gallery-backend1.herokuapp.com/emp/seeall/' + uid)
            .then(response => {
                setImagesList(response.data);
            })
            .catch((error) => {
                console.log(error);
            })
    }, [])

    function viewImages() {
        return imagelist.reverse().map((currentrow, index) => {
        
            return (
                <tr key={index}>
                    <td>{currentrow._id} </td>
                    <td>{currentrow.catagory.join(",")} </td>
                    <td width={270}><img src={currentrow.img_path} width={250} /> </td>
                    <td width={200}>{currentrow.description} </td>
                    <td>{currentrow.createdAt} </td>
                </tr>
            )
        })
    };

    let name = sessionStorage.getItem('uid');
    if (name == null) {
        return (<Redirect to="/login" />)
      }
    return (

        <div>
            <NavigationBar />
            <br /><br />
            <center><h3>All Uploaded Images</h3></center><br/><br/>
            <Container style={{overflowX:'auto'}}>
            <Table striped bordered hover variant="primary">
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>Catagory</th>
                        <th>Img Path</th>
                        <th>Description</th>
                        <th>Time of Create</th>
                    </tr>
                </thead>

                <tbody>
                    {viewImages()}
                </tbody>
            </Table>
            </Container>
            <br /><br />
        </div>
    )
}

export default UserViewImages;
