import React, { useState, useEffect } from 'react'
import axios from 'axios'
import NavigationBar from './NavigationBar';
import Table from 'react-bootstrap/Table';
import Container from 'react-bootstrap/Container';
import { Redirect } from 'react-router';

function ViewAllUserImagesAdmin() {
    const [Imglist, setImgList] = useState([]);
    const [msg, setMessage] = useState("");
    
    useEffect(() => {
        axios.get('https://image-gallery-backend1.herokuapp.com/emp/ViewallUsrImgAdmin')
          .then(response => {
            console.log(response.data)
            setImgList(response.data);
          })
          .catch((error) => {
            console.log(error);
          })
      }, [])
    
      function viewImgList() {
        return Imglist.map((currentrow, index) => {
          return (
    
            <tr key={index}>
              <td>{currentrow.authorname}</td>
              <td>{currentrow.authoremail}</td>
              <td>{currentrow.catagory.join(",")}</td>
              <td width={350}><img src={currentrow.img_path} width={250} alt=""/></td>
              <td>{currentrow.description}</td>
              <td>{currentrow.authorid}</td>
              <td><button onClick={() => removeRow(index)} className="btn btn-danger">Delete</button></td>
            </tr>
            
          );
        });
      }

      // function removeRow(index) {
      //   var tempImglist = [...Imglist]; // make a new copy of array instead of mutating the same array directly. 
      //   let removerow = tempImglist.splice(index, 1);
      //   console.log("hi");
      //   console.log(removerow[0]._id)
      //   axios.delete('https://image-gallery-backend1.herokuapp.com/emp/remove/' + removerow[0]._id)
      //     .then(res => {
      //       console.log(res.data)
      //       setMessage('SUCCESSFULLY DELETED')
      //       setImgList(tempImglist)
      //     })
      //     .catch(err => {
      //       console.log(err)
      //       setMessage('INVALID EMAIL ID')
      //     })
      // }


      function removeRow(index){
        var templist = [...Imglist];
        let re = templist.splice(index,1);
         console.log("hello")
         console.log(templist)
        axios.delete('https://image-gallery-backend1.herokuapp.com/emp/remove/'+re[0]._id)
        .then(res=>{
            console.log(res.data)
            setMessage(<center>Image Deleted Succesfully.</center>);
            setImgList(templist)
        })
        .catch(err=>{
            console.log(err)
            setMessage('INVALID Image ID');
        })
    }
    
    let admin = sessionStorage.getItem('Key_Veriable')
    if (admin == null) {
      return (<Redirect to="/adminlogin" />)
    }
    return (
        <div>
        <NavigationBar />
        <br />
        <center>
        <h3>ALL USER UPLOADED IMAGES DETAILS</h3>
        </center><br/>
        <b style={{ color: "red" }}>{msg}</b>
        <Container style={{overflowX:'auto'}}>
        <Table striped bordered hover variant="primary">
          <thead>
            <tr>
              <th>Name</th>
              <th>Email</th>
              <th>CATAGORY</th>
              <th>IMAGE PATH</th>
              <th>DESCRIPTION</th>
              <th>ID</th>
            </tr>
          </thead>
  
          <tbody>
            {viewImgList()}
          </tbody>
          </Table>
          </Container>
      </div>
    )
  }

export default ViewAllUserImagesAdmin
