import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Col, Container, Row, Button, Card, Tabs, Tab, CardGroup } from 'react-bootstrap';
// import Navbars from './Navbars';
import SimpleDateTime from 'react-simple-timestamp-to-date';
import NavigationBar from './NavigationBar';
import { useHistory, useParams } from 'react-router';
import { Link } from 'react-router-dom';


function AboutImages(props) {
  const imageid = useParams();
    const [Imglist, setImgList] = useState([]);
    useEffect(() => {
        let imageid = localStorage.getItem('readmore');
        axios.get('https://image-gallery-backend1.herokuapp.com/emp/readimage/' + imageid)
            .then(response => {
                setImgList(response.data);
            })
            .catch(err => {
                console.log(err)
            })

    })

    function viewImgList() {
        return Imglist.map((currentrow, index) => {
          return (
    <>
            
                <center><div class="card"  style={{borderRadius:'20px',width:'50%'}} key={index}>
                <img src={currentrow.img_path} alt=""  style={{borderRadius:'20px'}}/>
                          <div class="card-body">
                            <center><h5 class="card-title">{currentrow.catagory.join(",")}</h5></center>
                            <center><p class="card-text">{currentrow.description}</p></center>
                            <center><p class="card-text"><small class="text-muted">Last updated 3 mins ago</small></p></center>
                          </div>
                         <center> <SimpleDateTime dateFormat="DMY" dateSeparator="/" timeSeparator=":">{currentrow.createdAt}</SimpleDateTime></center>
                               <center> <Card.Body>
                    <Card.Title style={{ }}>by -- {currentrow.authorname}.</Card.Title>
                    </Card.Body></center>
                    <center><Button variant="dark" size="sm" as={Link} to="/userafterlogin">Back</Button></center>
  
            </div></center>
           
            
    </> 
            
          );
        });
      }


      

    return (
        <>
            <NavigationBar />
            <br /><br />
                {viewImgList()}



            <br /><br />
        </>
    )
}
export default AboutImages;