import React, { useState } from 'react'
import axios from 'axios';
import NavigationBar from './NavigationBar';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { Redirect } from 'react-router'


function UserLogin(props) {
  const [eemail, setEmpEmail] = useState("");
  const [epass, setEmpPass] = useState("");;
  const [msg, setMessage] = useState("");;

  const onChangeEmpEmail = (e) => setEmpEmail(e.target.value);
  const onChangeEmpPass = (e) => setEmpPass(e.target.value);

  // console.log(onChangeEmpEmail);

  const handleSubmit = (evt) => {
    evt.preventDefault();

    // console.log(`UID: ${userid}`);
    console.log(`EMAIL: ${eemail}`);
    console.log(`PASS: ${epass}`);

    const emplogininfo = {
      empemail: eemail,
      emppass: epass
    }

    axios.post('https://image-gallery-backend1.herokuapp.com/emp/logincheck', emplogininfo)
      .then(res => {
        console.log(res.data)
            setMessage('LOGIN SUCCESSFUL')
            // sessionStorage.setItem("user", userid);
            sessionStorage.setItem("useremail", res.data[0].empemail);
            sessionStorage.setItem("userimage", res.data[0].empimage);
            sessionStorage.setItem("username", res.data[0].empname);
            sessionStorage.setItem("uid", res.data[0]._id);
            props.history.push('/userafterlogin')
      })
      .catch(err => {
        console.log(err)
        setMessage('INVALID UID OR PASSWORD')
      })
     
    // setUserId('')
    setEmpEmail('')
    setEmpPass('')
  }

//  var userid = sessionStorage.getItem('uid')

 if (sessionStorage.getItem('uid')!= null)       return <Redirect to="/userafterlogin" />

 if (sessionStorage.getItem('Key_Veriable')!= null)       return <Redirect to="/adminlogin" />

  return (
    <div>
      <NavigationBar />
      <br /><center>
      <h3>USER LOGIN</h3>
      </center><br/>
      {/* <b style={{ color: "red" }}> {msg} </b>
      <center>
      <form onSubmit={handleSubmit}>
        <input type="email" value={eemail}
          onChange={onChangeEmpEmail}
          placeholder="Enter Email"
          required style={{width:'50%'}}/>
        <br /><br />

        <input type="password" value={epass}
          placeholder="Enter Password"
          onChange={onChangeEmpPass}
          required style={{width:'50%'}}/>
        <br /><br />
        <input type="submit" value="LOGIN" className="btn btn-success" style={{width:'50%'}}/>
      </form>
      </center> */}



      <b style={{ color: "red" }}> {msg} </b>


      <center>
      <form onSubmit={handleSubmit}>
        <Box
      component="form"
      sx={{
        '& > :not(style)': { m: 1, width: '50%' },
      }}
      noValidate
      autoComplete="off"
    >
      
      <TextField id="outlined-basic" label="Email" variant="outlined" value={eemail}
          onChange={onChangeEmpEmail} color="success"/><br/><br />
      <TextField id="filled-basic" label="Password" type="password" variant="filled" value={epass} onChange={onChangeEmpPass} color="success"/><br/>
    </Box>
    <br /><br />
        <input type="submit" value="LOGIN" className="btn btn-success" style={{width:'50%'}}/>
    </form>
    </center>

    </div>
  )
}

export default UserLogin
