import React, { useState, useEffect } from "react";
import { Redirect } from "react-router-dom";
import axios from 'axios';
import NavigationBar from './NavigationBar';

function UserDeleteImages() {
    const [imageid, setImageid] = useState("");
    const [msg, setMessage] = useState("");

    const onChangeImageid = (e) => {
        setImageid(e.target.value);
        setMessage(''); //REMOVE ERROE MSG
    }

    const handleSubmit = (evt) => {
        evt.preventDefault();
        
        axios.delete('https://image-gallery-backend1.herokuapp.com/emp/removeimagefromuser/' + imageid)
        .then(res => {
            console.log(res.data)
            setMessage('SUCCESSFULLY DELETED')
        })
        .catch(err => {
            console.log(err)
            setMessage('INVALID IMAGE ID')
        })
        
        setImageid('')
    }

    let name = sessionStorage.getItem('uid');
  // console.log(authuser)
  if (name == null) {
    return (<Redirect to="/login" />)
  }
    return (
        <div>
            <NavigationBar/>
            <br /><br />
            <center><h3 >ENTER IMAGE ID FOR DELETE</h3></center>
            <b style={{ color: "red" }}>{msg}</b>
            <center>
            <form onSubmit={handleSubmit}>
                <input type="text" value={imageid}
                    onChange={onChangeImageid}
                    placeholder="IMAGE ID"
                    required style={{width:'50%'}}/>
                <br />
                <br />
                <input type="submit" value="DELETE Image" className="btn btn-danger" style={{width:'50%'}}/>
            </form>
            </center>
        </div>
    )
}

export default UserDeleteImages;
