import React, { useState } from 'react'
import axios from 'axios';
import NavigationBar from './NavigationBar';
import { Redirect } from 'react-router';
import ReactTagInput from "@pathofdev/react-tag-input";
import "@pathofdev/react-tag-input/build/index.css";

function UploadImage() {

    let authuser = sessionStorage.getItem('useremail');
    // let name = sessionStorage.getItem('username');
    let uid = sessionStorage.getItem('uid');
    let username = sessionStorage.getItem('username');


    // const [img_id, setEmpNam] = useState("");
    const [catagory, setcatagory] = useState("");
    const [img_path, setimg_path] = useState("");
    const [description, setdescription] = useState("");
    const [msg, setMessage] = useState("");
    const [tags, setTags] = useState([])

    const onChangeCatagory = (e) => setcatagory(e.target.value);
    const onChangeImg_path = (e) => setimg_path(e.target.value);
    const onChangeDescription = (e) => setdescription(e.target.value);
        
    const handleImage = async e => {
        e.preventDefault()
        let img = e.target.files[0]
        if (!img) return alert("File not exist.")
        //5242880 == 5 mb
        if (img.size > 1024 * 1024 * 10) return alert("Size too large!")
        if (img.type !== 'image/jpeg' && img.type !== 'image/png') return alert("File format is incorrect.")
    
        let data = new FormData()
        data.append('file', img)
        data.append('upload_preset', "mainak_image")
        data.append('cloud_name', "mainak487")
        fetch(' https://api.cloudinary.com/v1_1/mainak487/image/upload', {
          method: "post",
          body: data
        })
          .then(res => res.json())
          .then(data => {
            setimg_path(data.url)
            console.log(data)
          })
          .catch(err => {
            console.log(err)
          })
      }


    // const onChangeEmpAddress = (e) => setEmpAddress(e.target.value);
    const handleSubmit = (evt) => {
        evt.preventDefault();
        console.log(`Form submitted:`);
        // console.log(`NAME: ${name}`);
        // console.log(`EMAIL: ${email}`);

        const imagenfo = {
            catagory: catagory,
            img_path: img_path,
            description: description,
            authorid: uid,
            authorname: name,
            authoremail: authuser,
            // uaddress: eaddress
        }

        axios.post('https://image-gallery-backend1.herokuapp.com/emp/upload', imagenfo)
            .then(res => {
                console.log(res.data)
                setMessage(<center>Image ADDED SUCCESSFUL</center>)
            })
            .catch(err=>{
                console.log(err);
            })

        setcatagory("");
        setimg_path('');
        setdescription('');
        setTags([]);
        // setEmpAddress('')

    }

    // style={{borderStyle: 'solid',width:'50%',borderRadius:'10px',backgroundImage:'linear-gradient(to right, deeppink , violet)'}}
    let name = sessionStorage.getItem('username');
    // console.log(authuser)
    if (name == null) {
      return (<Redirect to="/login" />)
    }
    return (
        <div>
            <NavigationBar />
            <br />
            {/* <center>
            </center> */}
            <center>
            <h4 style={{ color: "brown" }}> {msg}</h4>
            
                <form className="login-form" onSubmit={handleSubmit} >
                    <h3>Image Upload Panel</h3>

                    <div className="form-group" style={{width:'80%'}}>
                            <p>Image Catagory</p>

                            <input type="text" value={catagory} className="form-control" placeholder="Category"  onChange={onChangeCatagory} required style={{width:'60%'}}/>
                            {/* <ReactTagInput
                                tags={tags}
                                placeholder="Type and press enter"
                                maxTags={30}
                                editable={true}
                                readOnly={false}
                                removeOnBackspace={true}
                                onChange={(newTags) => setTags(newTags)}
                                required
                            /> */}
                        </div>

                    <div className="form-group">
                        <p>Image Upload</p>
                        <input type="file" className="form-control" placeholder="Image link"  onChange={handleImage} required style={{width:'50%'}}/>
                    </div>

                    <div className="form-group">
                        <p>Description</p>
                        <textarea value={description} onChange={onChangeDescription} className="form-control" placeholder="Details About Image..." rows="5" required style={{width:'50%'}}>
                        </textarea>
                    </div>

                    <div className="form-group">
                        <p>User Name</p>
                        <input type="text" className="form-control" value={name} disabled style={{width:'50%'}}/>
                    </div>

                    <div className="form-group">
                        <p>User Email</p>
                        <input type="text" className="form-control" value={authuser} disabled style={{width:'50%'}}/>
                    </div>

                    <input type="submit" value="Upload" className="btn btn-success" style={{width:'50%'}}/>

                </form>
                </center>
        </div>
    )
        }
    
export default UploadImage;
