import axios from "axios";
import React, { useState } from "react";
import { Button, Col, Container, Row } from "react-bootstrap";
// import SimpleDateTime from "react-simple-timestamp-to-date";
import NavigationBar from './NavigationBar';
// import SearchIcon from './img/search.svg';
// import './Home.css'

function NavSearch(props) {

    const [Imglist, setImgList] = useState([]);
    const [catagory, setcatagory] = useState("");
    const [msg, setMessage] = useState("");
    // const [result, setResult] = useState([]);

    const onChangeCat = (e) => {
        setcatagory(e.target.value);
        setMessage(''); //REMOVE ERROE MSG
    }

    const handleSubmit = (evt) => {
        evt.preventDefault();
        console.log(catagory)
        axios.get('https://image-gallery-backend1.herokuapp.com/emp/findimage/' + catagory)
            .then(res => {
                setImgList(res.data)
                // setMessage("Search results for "+catagory)
            })
            .catch(err => {
                setMessage("No Images found on this catagory")
            })

        setcatagory('')
    }

    function viewImgList() {
        return Imglist.map((currentrow, index) => {
          return (
    <>
            {/* <tr key={index}>
              <td>{currentrow.authorname}</td>
              <td>{currentrow.authoremail}</td>
              <td>{currentrow.catagory}</td>
              <td><img src={currentrow.img_path} width={250} alt=""/></td>
              <td>{currentrow.description}</td>
              <td>{currentrow.authorid}</td>
            </tr> */}
  
            <div class="card"  style={{borderRadius:'20px',cursor:'pointer'}} key={index}>
                <img src={currentrow.img_path} alt=""  style={{borderRadius:'20px',width:'100%'}} onClick={()=>readmore(index)} />
                          <div class="card-body">
                            <center><h5 class="card-title">{currentrow.catagory.join(",")}</h5></center>
                            {/* <center><p class="card-text">{currentrow.description}</p></center> */}
                            <center><p class="card-text" style={{color:'black',fontFamily:'serif'}}>by --  {currentrow.authorname}</p></center>
                          </div>
                    {/* <center><Button variant="dark" size="sm" onClick={()=>readmore(index)}>Show</Button></center> */}
  
            </div>
            
    </> 
            
          );
        });
      }


    function readmore(index) {
        var temp = [...Imglist];
        localStorage.setItem('readmore', temp[index]._id)
        props.history.push('/MoreAboutImages');
    }

    return (
        <>
            <NavigationBar />
            {/* <br /><br />
            <h3 >Enter Catagory</h3> */}
            <Container>
            <Row>
                <Col>
            <center><b style={{ color: "red" }}>{msg}</b></center>
            <center>
                <form onSubmit={handleSubmit}>
                    <div class="form-outline" style={{ marginTop: '20px' }}>
                        <div className="container" >
                          <input type="search" id="form1" class="form-control" placeholder="Search Images, place and people" value={catagory}
                           onChange={onChangeCat} aria-label="Search" style={{ height: '50px', border:"none",width:'50%',border:'1px solid black' }} required/>
                        </div>
                   </div>
                <input type="submit" value="Search" className="btn btn-dark" size="sm"  style={{marginTop:"10px",width:'50%' }}/> 
            </form>
            </center>
            </Col>
            
            </Row>
            </Container>
            <br /><br />
            <Container>
                    <div class="card-columns">
                            {viewImgList()}
                    </div>
            </Container>

        </>
    )
}
export default NavSearch;