import React, { useState } from 'react';
import NavigationBar from './NavigationBar';
import { Redirect } from 'react-router';

function AdminLoginHooks(props) {
  const [adminuserid, setAdminUserId] = useState("");
  const [adminpassword, setAdminPassword] = useState("");;
  const [msg, setMessage] = useState("");;

  const onChangeAdminUserId = (e) => setAdminUserId(e.target.value);

  const handleSubmit = (evt) => {
    evt.preventDefault();

    console.log(`EMAIL: ${adminuserid}`);
    console.log(`PASS: ${adminpassword}`);

    if ((adminuserid === "admin629") && (adminpassword === "admin629")) {
      sessionStorage.setItem("Key_Veriable", 'ADMIN')
      setMessage('WELCOME ADMIN')
      props.history.push('/adminafterlogin')

    }
    else
      setMessage(<center>INVALID UID OR PASSWORD</center>)

    setAdminUserId('')
    setAdminPassword('')

  }

  // if (sessionStorage.getItem('uid'))        return <Redirect to="/userafterlogin" />

  if (sessionStorage.getItem('uid')!= null)       return <Redirect to="/userafterlogin" />

  
  return (
    <div>

      <NavigationBar />
      <br />
      <center>
      <h3>ADMIN LOGIN</h3>
      </center><br/>
      <b style={{ color: "red" }}>{msg}</b>

      <br/><br/>
      <center>
      <form onSubmit={handleSubmit}>
        <input type="text" value={adminuserid}
          onChange={onChangeAdminUserId} placeholder="ADMIN USER ID"
          required style={{width:'50%'}}/>
        <br />
        <br />
        <input type="password" value={adminpassword}
          onChange={(e) => setAdminPassword(e.target.value)} placeholder="ADMIN PASSWORD"
          required style={{width:'50%'}}/>
        <br />
        <br />
        <input type="submit" value="ADMIN LOGIN" className="btn btn-danger" style={{width:'50%'}}/>
      </form>
      </center>
    </div>

  );
}
export default AdminLoginHooks