import React,{useState} from 'react';
import styled from 'styled-components';
import PinterestIcon from '@material-ui/icons/Pinterest';
import IconButton from "@material-ui/core/IconButton";
// import * as ReactBootStrap from "react-bootstrap";
// import ExpandMoreRoundedIcon from '@material-ui/icons/ExpandMoreRounded';
// import { useMediaQuery } from 'react-responsive'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
  import Button from 'react-bootstrap/Button';

function Header() {
    
    return (
        <div>

            <Wrapper>
                <LogoWrapper as={Link} to = "/" >
                {/* <IconButton>
                    <PinterestIcon />
                </IconButton> */}
                         <div>
                            <img src="./gmit.png" style={{width:"80px",height:"90px"}}/>
                        </div>
                </LogoWrapper>
                <HomePageButton as={Link} to = "/signup" >
                    <a >SignUp</a>
                </HomePageButton>
                <FollowingButton as={Link} to = "/login" >
                    <a >Login</a>
                </FollowingButton>
          <div >      
            <RightButton>
                <AboutButton as={Link} to = "/about" >
                    <a>About</a>
                </AboutButton >
                <BusinessButton style={{textAlign: 'right'}} as={Link} to = "/business" >
                    <a>Business</a>
                </BusinessButton>
                {/* <FinalButton style={{textAlign:'right'}} as={Link} to = "/final" >
                     <a>final</a>
                </FinalButton> */}
            <Button variant="outline-danger" as={Link} to = "/contactus" style={{borderRadius:'20px'}}>Contact us</Button>
            <Button variant="outline-danger" as={Link} to = "/adminlogin" style={{borderRadius:'20px',marginLeft:'20px'}}>Admin Login</Button>
            </RightButton>
            </div>
            </Wrapper>
        </div>
    )
}

export default Header


const Wrapper = styled.div`
    display:flex;
    align-items: center;
    height: 56px;
    padding: 12px 4px 4px 16px;
    color: black;
` 

const LogoWrapper = styled.div`
    .MuiSvgIcon-root{
        color: #e60023;
        font-size: 40px;
    }

`
const HomeButtons = styled.div`
    display:flex;
    height: 48px;
    min-width 123px;
    align-items: center;
    justify-content: center;
    border-radius: 24px;
    cursor: pointer;
`

const HomePageButton = styled(HomeButtons)`
    margin-left: 10px;
    background-color: #e60023;

    a {
        text-decoration: none;
        color: white;
        font-weight: 700;
    }
`

const FollowingButton = styled(HomeButtons)`
    background-color: white;
    

    a {
        text-decoration: none;
        color: black;
        font-weight: 700;
    }

    :hover {
        background-color: #e1e1e1;
    }
`

const RightButton = styled.div`
    display:flex;
    height: 48px;
    min-width 100px;
    align-items: center;
    // justify-content: center;
    cursor: pointer;
`

const AboutButton = styled(RightButton)`

    a {
        text-decoration: none;
        color: black;
        font-weight: 700;
    }

`

const BusinessButton = styled(RightButton)`
    a {
        text-decoration: none;
        color: black;
        font-weight: 700;
    }

`

