import React from 'react';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
// import Header from './Header';
import './about.css';
import Button from 'react-bootstrap/Button';
import {
    Link
  } from "react-router-dom";
//   import Card from 'react-bootstrap/Card';
  import Footer from './Footer';
import NavigationBar from './NavigationBar';


function About() {
    return (
        <div>
        <div>
            {/* <Header/> */}
            <NavigationBar/>
            <br></br>
         <div className="bg">
             <div className="bg-text">
         <Container>
            <Row>
                <Col style={{marginTop:'200px'}}><h1 style={{color:'white',fontFamily:'unset',fontSize:'50px'}}>When it comes to a<br></br> great idea, you know<br></br> it when you see it.</h1></Col>
            </Row>
            <Row>
                <Col>
                <br></br>
                    <Button variant="danger" as={Link} to = "/signup" style={{width:'100px'}}>Join Now</Button>{' '}
                </Col>
            </Row>
        </Container>
        </div>
         </div>
         
         </div>
       
             <Footer/>
         </div>
    )
}

export default About;
